$green: #99c39a;
$yellow: #fce242;
$pink: #f350a7;
$purple: #c15ab9;
$blue: #00a9e0;
$red: #fc2f39;
$lime: #ebdd66;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  img {
    max-height: 3.34em;
  }
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.section-wrap {
  width: 100%;
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  &.statement-section {
    background-color: $green;
  }
  &.commitment-section {
    background-color: $yellow;
  }
  &.promise-section {
    background-color: $pink;
  }
  &.services-section {
    background-color: $purple;
  }
  &.contact-section {
    background-color: $blue;
  }
  section {
    margin: 1rem 2rem;
  }
}
